import React from 'react';

const Square: React.FC = () => {
  const handleClick: React.MouseEventHandler<HTMLButtonElement> = (event) => {
    alert('Hello world!');
  };
  return (
    <button
      className="text-xl p-0 mr-0.5 mt-0.5 bg-blue-500 font-bold w-8 h-8"
      onClick={handleClick}
    >
      X
    </button>
  );
};

export default Square;
